<template>
  <div class="detail-container mx-auto">
    <Breadcrumb :bcs="bcs" />
    <DetailCard :cardInfo="detail" />
    <div class="detail-content clearfix">
      <div class="detail-left float-left">
        <Recommend title="推荐菜" :recommendList="detail.recommend" />
        <Comment :compdata="comments" />
      </div>
      <div class="detail-right float-right">
        <GueLike
          v-if="rightMenuData.length > 0"
          :gueItems="rightMenuData"
        ></GueLike>
      </div>
    </div>
  </div>
</template>

<script>
import Breadcrumb from "@/components/Breadcrumb";
import DetailCard from "@/components/DetailCard";
import GueLike from "@/components/GueLike";
import Recommend from "@/components/Recommend";
import Comment from "@/components/Comment";
export default {
  components: {
    Breadcrumb,
    DetailCard,
    GueLike,
    Recommend,
    Comment,
  },
  data() {
    return {
      bcs: [
        { title: "汕头美团", url: "/" },
        { title: "汕头美食", url: "/" },
        { title: "汕头生日蛋糕", url: "/" },
      ],
      detail: {}, // 这个数据按理不应该是这个组件请求的 或者应该传给这个组件什么参数 去请求的
      rightMenuData: [],
      comments: {},
    };
  },
  async created() {
    var res = await this.$api.fetchDetail();
    this.detail = res.data;
    var res1 = await this.$api.fetchRightMenu();
    this.rightMenuData = res1.data;

    var res2 = await this.$api.fetchComment();
    this.comments = res2.data;
  },
};
</script>

<style lang="less" scoped>
.detail-container {
  width: 1190px;
  .detail-left {
    width: 950px;
    .recommend-container {
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }
  .detail-right {
    .gue-like-container {
      background-color: #fff;
      border-radius: 4px;
      width: 230px;
      padding: 16px 20px 0;
      margin-top: 10px;
    }
  }
}
</style>