<template>
  <div v-if="compdata.comments">
    <div class="comment-header clearfix">
      <h3 class="float-left">{{ showComments.length }}条网友点评</h3>
      <div class="float-right sort">
        <a
          :class="{
            onSel: filterType === 'quality',
          }"
          @click="filterType = 'quality'"
          >质量排序</a
        >
        <a
          :class="{
            onSel: filterType === 'time',
          }"
          @click="filterType = 'time'"
          >时间排序</a
        >
      </div>
    </div>
    <div class="comment-container">
      <ul class="tab-wrapper clearfix">
        <li
          v-for="item in compdata.tags"
          :key="item.tag"
          @click="handleClick(item.tag)"
          :class="{
            onSel: selType === item.tag,
          }"
        >
          {{ item.tag }}({{ item.count }})
        </li>
        <!-- 好家伙 tab tag -->
      </ul>
      <div class="sea">
        <span @click="hasPic = !hasPic"
          ><b
            :class="{
              onSel: hasPic,
            }"
          ></b
          ><!-- react-text: 296 -->只看有图片的评论<!-- /react-text --></span
        >
      </div>
      <ShowComment
        v-for="item in showComments"
        :key="item.reviewId"
        :commentItem="item"
      />
    </div>
  </div>
</template>

<script>
import ShowComment from "./ShowComment";
export default {
  props: {
    compdata: {
      type: Object,
      require: true,
    },
  },
  components: {
    ShowComment,
  },
  data() {
    return {
      filterType: "quality",
      selType: "", // 我的想法是当filterType = 'tab' 就可以通过selType进行filter
      hasPic: false, // 有没有图片优先级最高 这里不考虑 有图片后还对评论进行筛选 不作多级筛选处理
    };
  },
  computed: {
    showComments() {
      if (this.hasPic) {
        return this.compdata.comments.filter(
          (item) => item.picUrls && item.picUrls.length > 0
        ); // 不清楚 如果没有图片还有没有picUrls属性，需要做容错处理
      }

      // 通过filterType 和 传进来的数据进行筛选
      // 依赖 filterType 和 compdata
      if (this.filterType === "all") {
        return this.compdata.comments;
      } else if (this.filterType === "quality") {
        // 数据里只分 true和false 那就把true放前面 false 放后面
        let arr1 = this.compdata.comments.filter((item) => item.quality);
        let arr2 = this.compdata.comments.filter((item) => !item.quality);
        return arr1.concat(arr2);
      } else if (this.filterType === "time") {
        let arr1 = [];
        this.$util.deepClone(this.compdata.comments, arr1);
        arr1.sort((a, b) => {
          return b.commentTime - a.commentTime;
        });
        return arr1;
      }

      return [];
      // 像tab那个应该是通过某个属性来筛选的 可以定义一个 属性外加filtertab进行筛选
      // 这里没有不清楚规则就不写了
    },
  },
  methods: {
    handleClick(tag) {
      if (this.selType == tag) {
        this.selType = "";
      } else {
        this.selType = tag;
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/css/common/var.less";
.comment-container {
  margin-top: 8px;
  background: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  padding: 0 20px 40px;
  color: #666;
  .tab-wrapper {
    padding-top: 15px;
    li {
      float: left;
      font-size: 14px;
      line-height: 34px;
      padding: 0 10px;
      border: 1px solid #e5e5e5;
      border-radius: 2px;
      margin: 0 10px 10px 0;
      cursor: pointer;
      &.onSel {
        background-color: #00c9b3;
        border: 1px solid #00c9b3;
        color: #fff;
      }
    }
  }
}
.comment-header {
  font-size: 20px;
  line-height: 26px;
  h3 {
    font-size: inherit;
    font-weight: normal;
  }
  a {
    display: inline-block;
    margin-left: 20px;
    cursor: pointer;
    color: #999;
    font-size: 12px;
    &.onSel {
      color: @green;
    }
  }
}
.sea {
  margin-top: 22px;
  span {
    display: inline-block;
    line-height: 20px;
    cursor: pointer;
    b {
      width: 15px;
      height: 15px;
      border-radius: 2px;
      border: 1px solid #ccc;
      display: inline-block;
      vertical-align: text-bottom;
      margin-right: 6px;
      box-sizing: content-box;
      &.onSel {
        background: url(https://s0.meituan.net/bs/file/?f=meis/meishi.web:assets/636c6b64fb0a3ed1.png@1ec9a80)
          no-repeat;
        background-size: cover;
        border-color: #00c9b3;
      }
    }
  }
}
</style>