<template>
  <div class="comment-item-container clearfix">
    <div class="avatar float-left">
      <el-avatar :size="60">user</el-avatar>
    </div>
    <div class="content float-right">
      <p class="name">{{ commentItem.userName }}</p>
      <p class="d-flex justify-content-between">
        <el-rate disabled :value="commentItem.star / 10"></el-rate>
        <span class="time">{{ commentItem.commentTime | formatTime }}</span>
      </p>
      <p class="comment">{{ commentItem.comment }}</p>
      <span class="review" v-if="commentItem.merchantComment">
        {{ commentItem.merchantComment }}
      </span>
      <p class="thumbnail clearfix">
        <img
          :class="{
            active: i === showIndex,
          }"
          @click="
            showIndex = i;
            isShow = true;
          "
          v-for="(imgInfo, i) in commentItem.picUrls"
          :key="imgInfo.id"
          :src="imgInfo.url"
          alt=""
        />
      </p>
      <p class="position-relative img-box" v-if="isShow">
        <i v-show="showIndex != 0" @click="showIndex --" class="el-icon-arrow-left position-absolute"></i>
        <img :src="commentItem.picUrls[showIndex].url" alt="" />
        <i v-show="showIndex < commentItem.picUrls.length - 1" @click="showIndex ++" class="el-icon-arrow-right right position-absolute"></i>
      </p>
      <p
        class="like"
        @click="commentItem.alreadyZzz = !commentItem.alreadyZzz"
        :class="{
          onSel: commentItem.alreadyZzz,
        }"
      >
        <!-- 这里父组件的数据 应该通知父组件在决定要不要改的 -->
        <i class="my-icon">&#xe600;</i>赞
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["commentItem"],
  filters: {
    formatTime(time) {
      var date = new Date(Number(time));
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      return `${year}年${month}月${day}日`;
    },
  },
  data() {
    return {
      showIndex: -1,
      isShow: false,
    };
  },
  methods : {
  }
};
</script>

<style lang="less" scoped>
@import "~@/css/common/var.less";
.comment-item-container {
  padding-top: 30px;
  .content {
    padding-left: 20px;
    width: 828px;
    box-sizing: content-box;
    border-bottom: 1px solid #e5e5e5;
    .name {
      font-size: 16px;
      color: #222;
      line-height: 22px;
      margin-bottom: 1px;
    }
    .time {
      font-size: 12px;
      line-height: 20px;
      color: #999;
    }
    .comment {
      font-size: 14px;
      line-height: 20px;
      padding-top: 13px;
    }
    .review {
      display: block;
      font-size: 14px;
      color: #31bbac;
      padding-top: 12px;
    }
    .like {
      display: block;
      cursor: pointer;
      font-size: 12px;
      color: #666;
      margin: 17px 0 16px;
      text-align: right;
      &.onSel {
        color: @green;
      }
      i {
        margin-right: 3px;
      }
    }
    .thumbnail {
      img {
        float: left;
        width: 140px;
        height: 140px;
        font-size: 0;
        border: 10px solid transparent;
        &.active {
          border-color: #f4f4f4;
        }
      }
    }
    .img-box {
      width: 460px;
      padding-top: 20px;
      img {
        max-width: 100%;
        display: block;
      }
      i[class^="el-icon-arrow-"] {
        font-size: 5rem;
        cursor: pointer;
        color: #fff;
        top: 50%;
        left: 1%;
        transform: translateY(-50%);
        &.right {
          left: auto;
          right: 1%;
        }
      }
    }
  }
}
</style>